import React from 'react';
import './page.css';

import Titlebar from '../components/Titlebar/Titlebar';
var Preload = require('react-preload').Preload;

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pageNumber: 0,
      urls: this.props.urls,
      texts: this.props.texts,
    }
  }

  pageHandler = (op) => {
    let newPage
    if (this.state.pageNumber === this.state.urls.length - 1 && op > 0) newPage = 0
    else if (this.state.pageNumber === 0 && op < 0) newPage = this.state.urls.length - 1;
    else newPage = this.state.pageNumber + op;
    console.log(newPage)
    this.setState({
      pageNumber: newPage,
    })
  }

  dotHandler = (idx) => {
    this.setState({
      pageNumber: idx,
    })
  }

  render() {
    if (this.props.bg) {
      document.body.style.backgroundColor = this.props.bg
      document.body.style.color = this.props.textColor || 'white';
    }

    const dotColor = this.props.bg ? {backgroundColor: 'white'} : {}

    if (this.props.widths && this.props.widths[this.state.pageNumber]) {
      document.body.style.backgroundImage = 'url(' + this.state.urls[this.state.pageNumber] + ')';
      console.log(document.body.style.backgroundImage)
    }
    return (
      <div>
      	<div className="titlebar-container"><Titlebar bg={this.props.titlebarBg ? this.props.titlebarBg : this.props.bg}/></div>
      	<div className='portfolio-container'>
            <Preload
                images={this.state.urls}
                autoResolveDelay={3000}
                onError={this._handleImageLoadError}
                onSuccess={this._handleImageLoadSuccess}
                resolveOnError={true}
                mountChildren={true}
            >
                {
                  (this.props.input[this.state.pageNumber] === "text")
                    ? <div className='text-container text-full' style={{backgroundColor: this.props.bg}}>{this.props.texts[this.state.pageNumber]}</div>       
                    : (this.props.widths && this.props.widths[this.state.pageNumber])
                        ? <div className="full-image"><img src={this.state.urls[this.state.pageNumber]} className="full-img" /></div>
                        : <div className='image-container'><img src={this.state.urls[this.state.pageNumber]} className="portfolio-img"/></div>
                }
            </Preload>
  	    </div>
        <div className="page-footer">
          <div className="page-title" style={(this.props.titlebarBg === "transparent") ? {backgroundColor: "rgba(255, 255, 255, 0.5)"} : {}}>{this.props.title || 'TITLE'}</div>
          <div className="dot-container" style={this.props.noDots ? {display:'none'} : {display: 'flex'}}>
            {this.state.urls.map((item, index) => (
              <span className={"dot" + (this.state.pageNumber === index ? ' selected' : '')} style={{...dotColor}} onClick={() => {this.dotHandler(index);}} />))
            }
          </div>
          <div className="page-link"><a href={"//" + this.props.link} className="link">{this.props.linkTitle}</a></div>
        </div>
        <div className="split-screen">
          <div className="split-left" onClick={() => {this.pageHandler(-1)}}/>
          <div className="split-right" onClick={() => {this.pageHandler(1)}} />
        </div>
      </div>
    );
  }
}

export default Page;
